.about-page {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    height: auto;
    margin: 30px;
    background-color: rgba(27, 27, 27, 0.733);

}
.about-main {
    color: #006eff;
    text-align: center;
    margin-top: 50px;
}
.about-main h1 {
    text-align: center;
    margin-bottom: 50px;
    color: #006eff;
    font-size: 28px;
}

.about-wildlife-health-grid {
    display: grid;
    width: 100%;
    height: auto;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    grid-gap: 0px;
    align-items: start;
    text-align: center;
    margin: 0 auto;
    padding: 0 auto;
    padding: 10px 300px;
    margin-top: 50px;
    color: whitesmoke;
}


.about-wildlife-health-grid .animation-container {
    grid-column: 1;
    grid-row: 1;
    margin: -150px auto;
    width: 100%;
    height: 700px;
}

.about-wildlife-health-grid .about-text {
    grid-column: 2;
    grid-row: 1;
    width: 450px;
    font-size: 21px;
    margin: 0 auto;
    margin-top: 70px;
}

.about-wildlife-health-grid .about-text p {
    color: rgba(221, 221, 221, 0.788);
}



.about-page .about-location {
    width: 100%;
    height: auto;
    margin: 0 auto;
    text-align: center;
    padding: 20px;
    margin-top: 50px;
}

.about-location h1 {
    color: whitesmoke;
    font-size: 2rem;
}

.about-page .about-location img {
    width: 50%;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    margin-top: 50px;
}

.about-location .google-maps-button {
    text-decoration: none;
    color: whitesmoke;
    transition: 0.3s ease-in-out;
}

.about-location .google-maps-button:hover {
    color: #006eff;
}

.about-page .email-registration-form {
    width: 100%;
    margin-top: 50px;
    color: whitesmoke;
    font-size: 1.5rem;
}

.about-page .email-registration-form form {
    width: 20%;
    margin: 0 auto;
    border-radius: 4px;
    margin-top: 30px;
    margin-bottom: 10px;
}

.about-page .email-registration-form input {
    padding: 10px 25px;
    cursor: pointer;
    font-size: 15px;
    background-color: whitesmoke;
    color: #006eff;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    transition: background-color 0.4s ease, transform 0.4s ease;
}

.about-page .email-registration-form input:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

.about-page .email-registration-form button {
    padding: 10px 15px;
    margin: 20px;
    cursor: pointer;
    font-size: 15px;
    background-color: #006eff;
    color: whitesmoke;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    transition: background-color 0.4s ease, transform 0.4s ease;
}

.about-page .email-registration-form button:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

.about-goals {
    color: whitesmoke;
    width: 1020px;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 100px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

.about-goals p {
    color: rgba(221, 221, 221, 0.788);
}

.about-line {
    width: 8px;
    height: 80%;
    margin-top: 40px;
    border-radius: 4px;
    background-color: #006eff;
}

.about-goals .goals {
    width: 500px;
    padding: 50px;
}

.about-goals .goals ul {
    padding: 0;
    padding-left: 20px;
}

.about-goals .goals li {
    margin-bottom: 22px;
    color: #006eff;
}

.about-goals .goals i {
    color: #006eff;
}

.about-goals .goals h1 {
    text-align: center;
    margin-bottom: 20px;
    padding: 10px;
}


