
.domesticated-info {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    color: whitesmoke;
}
.domesticated-info h1 {
    color: #006eff;
    font-size: 40px;
}
.domesticated-info h2 {
    font-size: 30px;
}
.domesticated-info span {
    color: #006eff;
}
.domesticated-info p {
    font-size: 20px;
    color: rgba(221, 221, 221, 0.788);
}
.domesticated-info i {
    margin-top: 25px;
    font-size: 45px;
    margin-bottom: 0;
}

.domesticated-content {
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 100px;
    grid-auto-rows: minmax(300px, auto);
}

.domesticated-item {
    width: 100%;
    background-color: rgba(27, 27, 27, 0.733);
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: transform 0.3s ease-in-out;
}

.domesticated-item:hover {
    transform: scale(1.2);
    cursor: zoom-in;
}

.domesticated-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-bottom: 0;
}

.domesticated-item h2 {
    color: whitesmoke;
    font-size: 20px;
    padding: 2px;
}

.domesticated-item button {
    width: 100%;
    height: 35px;
    font-size: 16px;
    text-align: center;
    align-items: center;
    justify-content: center;
    background-color: #006eff;
    color: whitesmoke;
    border: 1px solid #0e0e0e;
    transition: background-color 0.4s ease;
    cursor: pointer;
}

.domesticated-item button:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
}


