.staff-page {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    height: auto;
    margin: 30px;
    background-color: rgba(27, 27, 27, 0.733);

}

.staff-h1 h1 {
    text-align: center;
    margin-bottom: 50px;
    margin-top: 50px;
    color: #006eff;
    font-size: 28px;
}

.staff-info h1 {
    color: whitesmoke;
    font-size: 2rem;
}

.staff-info {
    color: whitesmoke;
    max-width: 1020px;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 50px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

.staff-line {
    width: 8px;
    height: 80%;
    margin-top: 55px;
    border-radius: 4px;
    background-color: #006eff;
}

.staff-info .staff-goals {
    width: 500px;
    padding: 50px;

}

.animation-container {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: top;
    align-items: top;
    margin: -95px -5px;
}

.staff-info .staff-goals ul {
    width: 100%;
    padding: 0;
    padding-left: 20px;
}

.staff-info .staff-goals i {
    color: #006eff;
}

.staff-info .staff-goals p {
    color: rgba(221, 221, 221, 0.788);
}

.staff-info .staff-goals li {
    margin-bottom: 22px;
    color: #006eff;
}


.staff-info .staff-goals h1 {
    text-align: center;
    margin-bottom: 20px;
    padding: 10px;
}

.staff-do {
    color: whitesmoke;
    max-width: 1020px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 50px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
    padding: 20px;
    gap: 20px;
}

.staff-do p {
    text-align: start;
    margin: 40px auto;
    color: rgba(221, 221, 221, 0.788);

}

.staff-do img {
    width: 100%;
    height: auto;
    margin: 25px -5px;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
}

@media screen and (max-width: 767px) {

    .staff-page {
        margin: 15px;
    }

    .staff-h1 {
        padding: 10px;
        font-size: 20px;
    }

    .staff-info h1 {
        color: whitesmoke;
        font-size: 25px;
    }

    .staff-info {
        color: whitesmoke;
        max-width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        margin: 0 auto;
        margin-top: 10px;
        margin-bottom: 50px;
        border: none;
        background-color: transparent;
    }

    .staff-line {
        display: none;
    }

    .staff-info .staff-goals {
        width: 100%;
        align-items: center;
        text-align: center;
        padding: 30px;
        padding-bottom: 0px;

    }

    .animation-container {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: top;
        align-items: top;
        margin: -55px -5px;
    }

    .staff-info .staff-goals ul {
        width: 100%;
        padding: 0;
        padding-left: 20px;
    }

    .staff-info .staff-goals i {
        color: #006eff;
        width: 100%;
    }

    .staff-info .staff-goals p {
        color: rgba(221, 221, 221, 0.788);
    }

    .staff-info .staff-goals li {
        margin-bottom: 22px;
        color: #006eff;
    }


    .staff-info .staff-goals h1 {
        text-align: center;
        margin-bottom: 20px;
        padding: 10px;
    }

    .staff-do {
        color: whitesmoke;
        max-width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        margin: 0 auto;
        margin-top: 0px;
        margin-bottom: 30px;
        border: none;
        background-color: transparent;
        padding: 20px;
        gap: 0px;
    }

    .staff-do p {
        text-align: start;
        margin: 10px auto;
        color: rgba(221, 221, 221, 0.788);
        text-align: center;

    }

    .staff-do img {
        width: 100%;
        height: auto;
        margin: 0 auto;
        border: 1px solid #0e0e0e;
        border-radius: 4px;
    }


}