.home {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    height: auto;
    margin: 30px;
    background-color: rgba(27, 27, 27, 0.733);

}

section .home-info {
    text-align: center;
    padding: 30px;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 100px;
    background: url(../../images/homemain.jpg) center no-repeat;
    filter: saturate(120%);
    border-radius: 4px;
    background-size: 100% auto;
    width: 100%;
    max-width: 1400px;
    max-height: 600px;
}

section .home-info h1 {
    position: relative;
    color: whitesmoke;
    font-size: 45px;
    padding: 10px;
    margin-top: 0;
    margin-bottom: 350px;
    z-index: 1;
}

section .home-info::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.233);
    z-index: -1;
}

section .home-info h3 {
    color: whitesmoke;
    font-size: 25px;
}

section .home-info p {
    color: whitesmoke;
    font-size: 20px;
    height: auto;
    margin: 0 auto;
    width: 100%;
    max-width: 1400px;
    height: auto;
}

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 100px;
    margin-top: 50px;
    margin-bottom: 20px;
    margin-left: 100px;
    margin-right: 100px;

}

.grid-text-left {
    margin-top: 70px;
    margin-left: 0px;
}

.grid-text-right {
    margin-top: 70px;
    margin-right: 0px;
}

.grid-item h1 {
    color: #006eff;
    font-size: 45px;

}

.grid-item h3 {
    color: whitesmoke;
    font-size: 25px;
}

.grid-item p {
    color: whitesmoke;
    font-size: 20px;
    color: rgba(221, 221, 221, 0.788);
}

.grid-item {
    align-items: center;
    text-align: center;
    transform: translateY(100px);
    opacity: 0;
    transition: transform 1s ease, opacity 1s ease;
}

.grid-item.active {
    transform: translateY(0);
    opacity: 1;
}

.grid-item img {
    border-radius: 4px;
    border: 1px solid #0e0e0e;
    width: 100%;
    max-width: 500px;
    height: auto;
    transition: transform 0.3s ease-in-out;
}

.grid-item img:hover {
    transform: scale(1.2);
    cursor: zoom-in;
}

.grid-item .home-button {
    padding: 10px 15px;
    margin: 20px;
    cursor: pointer;
    font-size: 15px;
    background-color: #006eff;
    color: whitesmoke;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    transition: background-color 0.4s ease, transform 0.4s ease;
}

.grid-item .home-button:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

.home-impact {
    text-align: center;
    margin-top: 150px;
    margin-bottom: 150px;
}

.home-impact h1 {
    font-size: 45px;
    color: #006eff;
}

.home-impact .impact-count1 {
    display: inline-block;
    margin: 20px;
    text-align: center;
    margin-right: 70px;
}

.home-impact .impact-count1 span {
    color: #006eff;
    font-size: 45px;
}

.home-impact .impact-count1 p {
    color: whitesmoke;
    font-size: 18px;
}

.home-impact .bar {
    width: 200px;
    height: 4px;
    margin: 0 auto;
    border-radius: 4px;
    background-color: #006eff;
    margin-bottom: 20px;
}

.accordion {
    display: flex;
    height: auto;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-bottom: 100px;
    color: white;
    flex-direction: column;
}

.accordion h1 {
    color: #006eff;
    font-size: 30px;
    margin-bottom: 15px;
}

.accordion .item {
    background-color: rgba(27, 27, 27, 0.733);
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    margin-bottom: 15px;
    padding: 20px;
    width: 500px;
    transition: background-color 0.4s ease;
}

.accordion .item.open {
    background-color: #0e0e0e;
    outline: none;
}

.accordion .item .title {
    color: whitesmoke;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    cursor: pointer;
    padding: 20px;
}

.accordion .item .content {
    color: rgba(221, 221, 221, 0.788);
    transition: all 0.5s cubic-bezier(0, 1, 0, 1);
    align-items: center;
    margin-top: 15px;
}

.accordion .item:hover {
    border: 1px solid #006eff;
}

@media (max-width: 768px) {
    .home {
        margin: 15px;
    }
    section .home-info {
        text-align: center;
        padding: 20px;
        background-size: 90% auto;
        width: 100%;
    }
    section .home-info h1 {
        font-size: 30px;
        margin-bottom: 60px;
    }
    section .home-info::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: transparent;
        z-index: -1;
    }
    section .home-info h3 {
        font-size: 20px;
        margin: 0 auto;
        padding: 0 auto;
        padding-top: 300px;
    }
    section .home-info p {
        font-size: 15px;
    }
    .grid-container {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 50px;
        margin: 0 auto;
        padding: 20px
    }
    .grid-text-left {
        margin: 0 auto;
    }
    .grid-text-right {
        margin: 0 auto;
    }
    .grid-item h1 {
        font-size: 25px;

    }
    .grid-item h3 {
        font-size: 20px;
        margin-bottom: 10px;
    }
    .grid-item p {
        font-size: 15px;
    }
    .grid-item {
        align-items: center;
        text-align: center;
        opacity: 0;
    }
    .grid-item img {
        width: 100%;
        max-width: 500px;
        height: auto;
        transition: none;
    }
    .grid-item img:hover {
        transform: none;
        cursor: zoom-in;
    }
    .grid-item .home-button {
        padding: 10px 15px;
        margin: 20px;
        font-size: 15px;
    }
    .home-impact {
        display: static;
        text-align: center;
        margin: 0 auto;
        padding: 20px;
        margin-top: 50px;
        margin-bottom: 100px;
    }
    .home-impact h1 {
        font-size: 35px;
        color: #006eff;
        margin-bottom: 20px;
    }
    .home-impact .impact-count1 {
        display: block;
        margin: 0 auto;
        padding: 20px;
        text-align: center;
    }
    .accordion {
        height: auto;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin-bottom: 30px;
        padding: 20px;
    }
    .accordion h1 {
        font-size: 20px;
        margin-bottom: 25px;
    }
    .accordion .item {
        margin-bottom: 20px;
        padding: 20px;
        width: 100%;
    }
    .accordion .item .title {
        color: whitesmoke;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;
        cursor: pointer;
        padding: 10px;
    }
}