.application-page {
    max-width: 100%;
    height: auto;
    margin: 0 auto;
    margin: 30px;
    padding: 50px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

.application-page .application-info {
    width: 60%;
    margin: 0 auto;
    text-align: center;
}

.application-page .application-info h1 {
    color: #006eff;
    font-size: 28px;
    margin-bottom: 10px;
}

.application-page .application-info p {
    color: whitesmoke;
    font-size: 18px;
}

.application-page .application-content {
    width: 60%;
    margin: 0 auto;
    padding: 30px;
    margin-top: 50px;
    margin-bottom: 50px;
    border: 4px solid #0e0e0e;
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 50px;
    background-color: rgba(27, 27, 27, 0.733);
}

.application-page .application-content h2 {
    color: #006eff;
    margin-bottom: 5px;
}

.application-page .application-content p {
    color: rgba(221, 221, 221, 0.788);
}

.application-apply .btnApplication {
    margin: 0 auto;
    display: flex;
    justify-content: center;

}

.application-apply .btnApplication button {
    padding: 15px 20px;
    margin: 20px;
    cursor: pointer;
    font-size: 15px;
    background-color: #006eff;
    color: whitesmoke;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    transition: background-color 0.4s ease, transform 0.4s ease;
}

.application-apply .btnApplication button:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

@media screen and (max-width: 767px) {

    .application-page {
        max-width: 100%;
        height: auto;
        margin: 0 auto;
        margin: 15px;
        padding: 30px;
        border: 8px solid #0e0e0e;
        border-radius: 4px;
        background-color: rgba(27, 27, 27, 0.733);
    }

    .application-page .application-info {
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }

    .application-page .application-info h1 {
        color: #006eff;
        font-size: 25px;
        margin-bottom: 10px;
    }

    .application-page .application-info p {
        color: whitesmoke;
        font-size: 18px;
    }

    .application-page .application-content {
        width: 100%;
        margin: 0 auto;
        padding: 30px;
        border: none;
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px;
        background-color: transparent;
        text-align: center;

    }

    .application-page .application-content h2 {
        color: #006eff;
        margin-bottom: 5px;
        font-size: 20px;
    }

    .application-page .application-content p {
        color: rgba(221, 221, 221, 0.788);
    }

    .application-apply .btnApplication {
        margin: 0 auto;
        display: flex;
        justify-content: center;

    }

    .application-apply .btnApplication button {
        padding: 10px 30px;
        text-align: center;
        margin: 20px;
        cursor: pointer;
        font-size: 15px;
        background-color: #006eff;
        color: whitesmoke;
        border: 1px solid #0e0e0e;
        border-radius: 4px;
        transition: background-color 0.4s ease, transform 0.4s ease;
    }

    .application-apply .btnApplication button:hover {
        background-color: rgba(27, 27, 27, 0.733);
        color: #006eff;
        border: 1px solid #ffffff;
        transform: scale(1.05);
    }

}