
/* Header styles */
header {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    margin: 30px;
    background-color: rgba(27, 27, 27, 0.733);
    position: relative;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 20px;
    padding-top: 15px;
}

.logo {
    display: flex;
    align-items: center;
}

.logo img {
    width: 160px;
    height: auto;
    margin-left: 30px;
    filter: drop-shadow(12px 12px 10px rgba(0, 0, 0, 0.7));
}

.navbar ul {
    list-style: none;
    margin-bottom: 115px;
    margin-right: 20px;
    margin-top: 0;
}

.navbar ul li {
    display: inline-block;
    position: relative;
}

.navbar ul li a {
    width: 150px;
    display: flex;
    align-items: center;
    padding: 20px 25px;
    color: whitesmoke;
    text-decoration: none;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    transition: color 0.4s ease;
}

.navbar ul li ul.dropdown li {
    display: block;
}

.navbar ul li ul.dropdown {
    width: 100%;
    position: absolute;
    border-radius: 4px;
    z-index: 999;
    display: none;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
}

.navbar ul li a:hover {
    color: #006eff;
}

.navbar ul li:hover ul.dropdown {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 150px;
}


/* Responsive Styles */
@media screen and (max-width: 767px) {
    header {
        height: auto;
    }

    .header {
        margin: 15px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
    }

    .logo {
        order: 1;
        margin: 0 auto;
        margin-right: 40px;
        width: 100%;
        padding: 0px;
        justify-content: center;
    }

    .logo img {
        width: 35%;
    }

    .navbar {
        order: 2;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        width: 100%;
    }

    .navbar ul {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
        width: 100%;
        justify-content: space-between;
    }

    .navbar ul li {
        width: calc(45% - 5px);
        padding: 10px;
        text-align: center;
    }

    .navbar ul li a {
        font-size: 16px;
    }

    .navbar ul li ul.dropdown {
        position: relative;
    }
}