.support-page {
    max-width: 100%;
    height: auto;
    margin: 0 auto;
    margin: 30px;
    padding: 50px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

.support-page .support-info {
    width: 50%;
    margin: 0 auto;
    text-align: center;
}

.support-page .support-info h1 {
    color: #006eff;
    font-size: 28px;
}

.support-page .support-info p {
    color: whitesmoke;
    font-size: 18px;
}

.support-page .support-content {
    width: 50%;
    border: 8px solid #0e0e0e;
    background-color: rgba(27, 27, 27, 0.733);
    padding: 30px;
    margin: 0 auto;
    text-align: center;
}

.support-page .support-content h2 {
    color: #006eff;
}

.support-page .support-content p {
    color: rgba(221, 221, 221, 0.788);
}

.support-page .support-content span {
    color: whitesmoke;
    font-size: 18px;
    line-height: 40px;
}

.support-page .support-calls {
    width: 60%;
    margin: 0 auto;
    margin-top: 50px;
    text-align: center;
    display: flex;
    justify-content: center;
    gap: 100px;
    color: whitesmoke;
}

.support-page .support-calls p {
    cursor: pointer;
    margin-top: 5px;
}

.support-page .support-calls p:hover {
    color: #006eff;
}


@media screen and (max-width: 767px) {

    .support-page {
        max-width: 100%;
        height: auto;
        margin: 0 auto;
        margin: 15px;
        padding: 30px;
        border: 8px solid #0e0e0e;
        border-radius: 4px;
        background-color: rgba(27, 27, 27, 0.733);
    }

    .support-page .support-info {
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }

    .support-page .support-info h1 {
        color: #006eff;
        font-size: 25px;
    }

    .support-page .support-info p {
        color: whitesmoke;
        font-size: 18px;
    }

    .support-page .support-content {
        width: 100%;
        border: none;
        background-color: transparent;
        padding: 30px;
        margin: 0 auto;
        text-align: center;
    }

    .support-page .support-content h2 {
        color: #006eff;
    }

    .support-page .support-content p {
        color: rgba(221, 221, 221, 0.788);
    }

    .support-page .support-content span {
        color: whitesmoke;
        font-size: 18px;
        line-height: 40px;
    }

    .support-page .support-calls {
        width: 100%;
        margin: 0 auto;
        margin-top: 40px;
        text-align: center;
        display: block;
        justify-content: center;
        line-height: 30px;
        color: whitesmoke;
    }

    .support-page .support-calls p {
        cursor: pointer;
        margin-top: 5px;

    }

    .support-page .support-calls p:hover {
        color: #006eff;
    }


}