.animals {
    display: grid;
    grid-template-rows: auto auto;
    gap: 30px;
    margin-left: 30px;
    margin-right: 30px;
}

.default-content {
    font-size: 20px;
    line-height: 26px;
    width: 50%;
    height: auto;
    margin: 0 auto;
    text-align: center;
}

.default-content h1 {
    color: #006eff;
    margin-top: 15px;
}

.default-content h2 {
    color: white;
}

.default-content p {
    color: rgba(221, 221, 221, 0.788);
}

.default-content i {
    color: #006eff;
}

.default-content .buttons .link-button {
    background-color: #006eff;
    color: white;
    padding: 10px 20px;
    margin-right: 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 19px;
    text-transform: uppercase;
    border: 1px solid #0e0e0e;
}

.default-content .buttons button {
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}


.default-content .buttons button:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: white;
    border: 1px solid #006eff;
}

.animals .animals-navbar {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
    padding: 40px;
    padding-left: 20px;
    line-height: 30px;
    height: 15vh;
    gap: 20px;
    display: flex;
    flex-direction: rows;
    align-items: center;
    justify-content: center;
}

.animals .animals-navbar button {
    width: 100%;
    height: 50px;
    font-size: 16px;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
    cursor: pointer;
    background-color: #006eff;
    color: whitesmoke;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    transition: background-color 0.4s ease, transform 0.4s ease;

}

.animals .animals-navbar button:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

.animals .animals-navbar button.active {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

.animals .animals-navbar button:first-child {
    margin-right: 150px;
}

.content-container {
    padding: 50px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

