/* footer styles */
.footer {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    margin: 30px;
    background-color: rgba(27, 27, 27, 0.733);
}
.footer-section {
    display: flex;
    justify-content: space-between;
    color: white;
    padding: 20px 50px;
}
.footer-section .footer-social {
    margin-top: 20px;
    width: 300px;
    align-items: center;
    display: flex;
    gap: 55px;
}
.footer-section .footer-social i {
    margin: 0 auto;
    padding: 0;
    font-size: 24px;
    cursor: pointer;
    padding: 0px;
    transition: transform 0.4s ease-in-out;
}
.footer-social a:nth-child(3) i.fa-brands.fa-instagram {
    font-size: 26px;
}
.footer-social a:nth-child(4) i.fa-brands.fa-youtube {
    font-size: 27px;
}
.footer-section .footer-social i:hover {
    transform: scale(1.3);
}
.footer-section .footer-rights {
    display: flex;
    align-items: end;
    font-size: 10px;
    margin-left: 50px;
    color: #bbbbbb;
}
.footer-info {
    width: 20%;
    margin-top: 20px;
}
.footer-info p {
    margin-bottom: 5px;
}
.footer-info p span {
    font-size: 20px;
}
.footer-info a {
    text-decoration: none;
    color: whitesmoke;
}
.footer-info a:hover {
    color: #006eff;
}
.footer-info h2 {
    text-align: center;
    font-size: 13px;
    color: #3f3f3f;
}
.footer-info .footer-email {
    color: whitesmoke;
    text-decoration: none;
}
.footer-line {
    width: 8px;
    border-radius: 4px;
    background-color: #006eff;
    margin: 5px 0;
}
.footer-navigation {
    align-items: center;
    margin-top: 35px;
    margin-right: 0px;
    text-align: end;
}
.footer-navigation a {
    display: block;
    text-decoration: none;
    color: #f7f7f7;
    font-size: 20px;
    font-weight: bold;
    transition: color 0.3s ease;
    margin: 15px 0;
}
.footer-navigation a:hover {
    color: #006eff;
}

/* media styles */
@media screen and (max-width: 767px) {

    .footer {
        height: auto;
        margin: 15px;
    }
    .footer-section {
        align-self: flex-start;
        margin: 0 auto;
        padding: 20px;
    }
    .social-icons-container {
        height: 200px;
    }
    .footer-section .footer-social {
        margin-top: 50px;
        margin-bottom: 0px;
        width: auto;
        align-items: start;
        display: flex;
        gap: 70px;
    }
    .footer-section .footer-rights {
        display: flex;
        align-items: end;
        margin: 40px -80px;
        padding-right: 60px;
    }
    .footer-section .footer-rights h1{
        text-align: center;
        font-size: 10px;
        margin: 0 auto;
        margin-bottom: 10px;
        color: #bbbbbb;
    }
    .footer-info {
        width: 40%;
    }
    .footer-info p {
        margin-bottom: 5px;
        font-size: 14px;
    }
    .footer-info p span {
        font-size: 16px;
    }
    .footer-line {
        display: none;
    }
    .footer-navigation {
        margin-top: 55px;
    }
    .footer-navigation a {
        font-size: 15px;
    }
}