

.mammals-info {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    color: whitesmoke;
}

.mammals-info h1 {
    color: #006eff;
    font-size: 40px;
}

.mammals-info h2 {
    font-size: 30px;
}

.mammals-info span {
    color: #006eff;
}

.mammals-info p {
    font-size: 20px;
    color: rgba(221, 221, 221, 0.788);
}

.mammals-info i {
    margin-top: 25px;
    font-size: 45px;
    margin-bottom: 0;
}

.mammals-content {
    width: 100%;
    height: auto;
    margin: 0 auto;
    padding: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 100px;
    grid-auto-rows: minmax(300px, auto);
}

.mammals-item {
    width: 100%;
    background-color: rgba(27, 27, 27, 0.733);
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: transform 0.3s ease-in-out;
}

.mammals-item:hover {
    transform: scale(1.2);
    cursor: zoom-in;
}

.mammals-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-bottom: 0;
}

.mammals-item h2 {
    color: whitesmoke;
    font-size: 20px;
    padding: 2px;
}


@media screen and (max-width: 767px) {

    .mammals-info {
        width: 100%;
        margin: 0 auto;
        text-align: center;
        color: whitesmoke;
        margin-bottom: 40px;
    }

    .mammals-info h1 {
        color: #006eff;
        font-size: 30px;
    }

    .mammals-info h2 {
        font-size: 25px;
    }

    .mammals-info span {
        color: #006eff;
    }

    .mammals-info p {
        font-size: 15px;
        color: rgba(221, 221, 221, 0.788);
    }

    .mammals-info i {
        display: none;
    }

    .mammals-content {
        width: 100%;
        height: auto;
        margin: 0 auto;
        padding: 0px;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 50px;
        grid-auto-rows: auto;
    }

    .mammals-item {
        width: 100%;
        background-color: rgba(27, 27, 27, 0.733);
        border: 1px solid #0e0e0e;
        border-radius: 4px;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: transform 0.3s ease-in-out;
    }

    .mammals-item:hover {
        transform: scale(1.2);
        cursor: zoom-in;
    }

    .mammals-item img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        margin-bottom: 0;
    }

    .mammals-item h2 {
        color: whitesmoke;
        font-size: 15px;
        padding: 2px;
    }

}