.health-page {
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    margin: 30px;
    padding: 50px;
    background-color: rgba(27, 27, 27, 0.733);
    position: relative;
}
.health-h1 h1 {
    text-align: center;
    margin-bottom: 50px;
    color: #006eff;
    font-size: 28px;
}

.section {
    color: whitesmoke;
    width: 80%;
    height: auto;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 50px;
    margin: 0 auto;
    padding: 50px;
    padding-bottom: 80px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

.section-header h1 {
    text-align: center;
    color: #006eff;
    margin-top: 10px;
    margin-bottom: 50px;
    font-size: 1.9rem;
}

.section-header p {
    font-size: 1.4rem;
    width: 80%;
    margin: 0 auto;
    color: rgba(221, 221, 221, 0.788);
}

.section-content {
    text-align: center;
    font-size: 1.2rem;
    border-radius: 4px;
    padding: 20px;
    margin-top: 15px;
    border: 4px solid #0e0e0e;
    background-color: #006eff42;
}

.section img {
    width: 100%;
    height: auto;
    padding: 60px;
    margin: -80px auto;
}


@media screen and (max-width: 767px) {

    .health-page {
        border: 8px solid #0e0e0e;
        border-radius: 4px;
        margin: 15px;
        padding: 20px;
        background-color: rgba(27, 27, 27, 0.733);
        position: relative;
    }
    
    .section {
        color: whitesmoke;
        width: 100%;
        height: auto;
        display: grid;
        grid-template-columns: 1fr;
        gap: 30px;
        margin: 0 auto;
        padding: 20px;
        padding-bottom: 30px;
        border: none;
        background-color: transparent;
    }
    
    .section-header h1 {
        text-align: center;
        color: #006eff;
        margin-top: 0px;
        margin-bottom: 50px;
        font-size: 25px;
    }
    
    .section-header p {
        font-size: 20px;
        width: 100%;
        margin: 0 auto;
        color: rgba(221, 221, 221, 0.788);
        text-align: center;
    }
    
    .section-content {
        text-align: center;
        font-size: 15px;
        border-radius: 4px;
        padding: 20px;
        margin-top: 15px;
        border: 4px solid #0e0e0e;
        background-color: #006eff42;
    }
    
    .section img {
        width: 100%;
        height: auto;
        padding: 0px;
        margin: 0 auto;
        margin-top: 20px;
    }
    

}