.donation-page {
    max-width: 100%;
    height: auto;
    margin: 0 auto;
    margin: 30px;
    padding: 50px;
    border: 8px solid #0e0e0e;
    border-radius: 4px;
    background-color: rgba(27, 27, 27, 0.733);
}

.donation-content {
    max-width: 800px;
    text-align: center;
    margin: 0 auto;
}

.donation-content h1 {
    font-size: 28px;
    margin-bottom: 10px;
}

.donation-content p {
    font-size: 18px;
    color: whitesmoke;
    margin-bottom: 30px;
}

.donation-help {
    text-align: center;
    max-width: 60%;
    height: auto;
    margin: 0 auto;
}

.row.donation-info-grid {
    border: 8px solid #000000;
    border-radius: 4px;
    padding: 50px;
    margin: 50px;
    background-color: rgba(27, 27, 27, 0.733);
}

.donation-help h2 {
    color: #006eff;
}

.donation-help p {
    color: rgba(221, 221, 221, 0.788);
}

.donation-form {
    background-color: rgba(27, 27, 27, 0.733);
    border-radius: 4px;
    margin: 0 auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border: 8px solid #0e0e0e;
    max-width: 70%;
    padding: 50px;
}

.donation-form h1 {
    text-align: center;
    color: #006eff;
    margin-bottom: 50px;
}

.donation-form .form-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.donation-form .form-section {
    grid-column: span 2;
    margin-bottom: 20px;
}

.donation-form .form-section h2 {
    margin-top: 0;
    color: #006eff;
    font-size: 20px;
}

.donation-form input[type="text"],
input[type="email"],
input[type="tel"],
select,
textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    font-size: 16px;
    transition: border-color 0.3s ease;
}

.donation-form textarea {
    resize: none;
    width: 100%;
    height: 25.7vh;
}


.donation-form select {
    appearance: none;
    padding-right: 30px;
    background-repeat: no-repeat;
    background-position: right center;
    cursor: pointer;
}

.donation-form .submitBtn {
    margin: 0 auto;
    width: 100%;
    margin-bottom: 20px;
    margin-left: 50%;
    align-items: start;
    cursor: pointer;
    font-size: 15px;
    background-color: #006eff;
    color: whitesmoke;
    border: 1px solid #0e0e0e;
    border-radius: 4px;
    transition: background-color 0.4s ease, transform 0.4s ease;
}

.donation-form .submitBtn:hover {
    background-color: rgba(27, 27, 27, 0.733);
    color: #006eff;
    border: 1px solid #ffffff;
    transform: scale(1.05);
}

.donation-form input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
select:focus,
textarea:focus {
    border-color: #006eff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 110, 255, 0.5);
}

.donation-form input[type="checkbox"] {
    cursor: pointer;
}

.donation-form label {
    display: inline-block;
    font-size: 16px;
    margin-left: 5px;
    color: #333;
    grid-column: span 2;
}

.donationAmount::placeholder {
    font-size: 16px;
    padding-right: 30px;
}

